
.food-wrapper {
  position: fixed;
  right: 0;
  left: 0;
  top: 0;
  bottom: 48px;
  z-index: 20;
  background: #fff;
  transform: translate3d(0, 0, 0);
  transition: all 0.3s linear;
}
.food-wrapper.move-enter-active,
.food-wrapper.move-leave-active {
  transform: translate3d(100%, 0, 0);
}
.food-wrapper .image-header {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;
}
.food-wrapper .image-header img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.food-wrapper .image-header .icon-back {
  position: absolute;
  top: 10px;
  left: 0;
}
.food-wrapper .image-header .icon-back .icon-arrow_lift {
  display: block;
  padding: 10px;
  font-size: 20px;
  color: #f2f2f2;
}
.food-wrapper .content {
  padding: 18px;
  position: relative;
}
.food-wrapper .content .title {
  line-height: 14px;
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
}
.food-wrapper .content .detail {
  margin-bottom: 18px;
  line-height: 10px;
  height: 10px;
  font-size: 0;
}
.food-wrapper .content .detail .sell-count,
.food-wrapper .content .detail .rating {
  font-size: 10px;
  color: #93999f;
}
.food-wrapper .content .detail .sell-count .sell-count,
.food-wrapper .content .detail .rating .sell-count {
  margin-right: 12px;
}
.food-wrapper .content .price {
  font-weight: 700;
  line-height: 24px;
}
.food-wrapper .content .price .now {
  margin-right: 8px;
  font-size: 14px;
  color: #f01414;
}
.food-wrapper .content .price .old {
  text-decoration: line-through;
  font-size: 10px;
  color: #93999f;
}
.food-wrapper .content .cartcontrol-wrapper {
  position: absolute;
  right: 12px;
  bottom: 12px;
  z-index: 30;
}
.food-wrapper .content .buy {
  position: absolute;
  right: 18px;
  bottom: 18px;
  z-index: 50;
  height: 24px;
  line-height: 24px;
  padding: 0 12px;
  box-sizing: border-box;
  font-size: 10px;
  border-radius: 12px;
  color: #fff;
  background: #00a0dc;
  transition: all 0.2s;
  opacity: 1;
}
.food-wrapper .content .buy.fade-enter-active,
.food-wrapper .content .buy.fade-leave-active {
  opacity: 0;
}
.food-wrapper .info {
  padding: 18px;
}
.food-wrapper .info .title {
  line-height: 14px;
  margin-bottom: 6px;
  font-size: 14px;
  color: #07111b;
}
.food-wrapper .info .text {
  line-height: 24px;
  padding: 0 8px;
  font-size: 12px;
  color: #4d555d;
}
.food-wrapper .rating {
  padding-top: 18px;
}
.food-wrapper .rating .title {
  line-height: 14px;
  margin-left: 18px;
  font-size: 14px;
  color: #07111b;
}
.food-wrapper .rating .rating-wrapper {
  padding: 0 18px;
}
.food-wrapper .rating .rating-wrapper .rating-item {
  position: relative;
  padding: 16px 0;
  position: relative;
}
.food-wrapper .rating .rating-wrapper .rating-item:after {
  display: block;
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.food-wrapper .rating .rating-wrapper .rating-item .user {
  position: absolute;
  right: 0;
  top: 16px;
  font-size: 0;
  line-height: 12px;
}
.food-wrapper .rating .rating-wrapper .rating-item .user .name {
  display: inline-block;
  vertical-align: top;
  font-size: 10px;
  color: #93999f;
  margin-right: 6px;
}
.food-wrapper .rating .rating-wrapper .rating-item .user .avatar {
  border-radius: 50%;
}
.food-wrapper .rating .rating-wrapper .rating-item .time {
  font-size: 10px;
  line-height: 12px;
  color: #93999f;
  margin-bottom: 6px;
}
.food-wrapper .rating .rating-wrapper .rating-item .text {
  line-height: 16px;
  font-size: 12px;
  color: #07111b;
}
.food-wrapper .rating .rating-wrapper .rating-item .text .icon-thumb_up,
.food-wrapper .rating .rating-wrapper .rating-item .text .icon-thumb_down {
  margin-right: 4px;
  line-height: 16px;
  font-size: 12px;
}
.food-wrapper .rating .rating-wrapper .rating-item .text .icon-thumb_up {
  color: #00a0dc;
}
.food-wrapper .rating .rating-wrapper .rating-item .text .icon-thumb_down {
  color: #93999f;
}
.food-wrapper .rating .rating-wrapper .no-rating {
  padding: 16px 0;
  font-size: 12px;
  color: #93999f;
}


